import Layout from '../components/layout';
import rooms from '../data/rooms.js';

const Index = (props) => (
	<div>
		{props.rooms.map((room, code) => <Layout {...props} key={code} room={room} />)}
	</div>

);


Index.getInitialProps = async function() {
	const data= {
		rooms
	};
	return data
}

export default Index;